body {
  margin: 0%;
  font-family: sans-serif;
}

header {
  background-color: #008b8b;
  justify-content: space-between;
  align-items: center;
  min-height: 70px;
  padding: 10px;
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.logo img {
  width: unset;
  height: 50px;
  margin-right: 10px;
  padding: 0;
}

nav a {
  color: #000;
  padding-right: 10px;
  font-weight: 600;
  text-decoration: none;
}

nav a:hover {
  color: #fff;
}

.imagen-info {
  align-items: flex-start;
  margin-bottom: 30px;
  display: flex;
}

.imagen-info img {
  border-radius: 8px;
  width: 15%;
  height: 50%;
  margin-top: 10px;
  padding: 5px;
  box-shadow: 2px 2px 8px #00000080;
}

h1 {
  text-align: center;
  color: #8ee3ee;
  text-shadow: 2px 2px #000;
  font-size: 36px;
  font-weight: bold;
}

.info * {
  margin: 10px;
}

.info p {
  text-align: justify;
  color: #333;
  margin-bottom: 1em;
  font-family: Open Sans, sans-serif;
  font-size: 18px;
  line-height: 1.5;
}

.razas a {
  color: #4d4d4d;
  letter-spacing: 2px;
  margin: 0;
  font-family: Montserrat, sans-serif;
  font-size: 18px;
  font-weight: bold;
  text-decoration: none;
  transition: all .2s ease-in-out;
}

.razas a:hover {
  color: #21d682d2;
  text-decoration: underline;
}

.contenedor {
  gap: 20px;
  display: flex;
}

.menu-lateral {
  background-color: #008b8b;
  flex: 1.1;
  position: relative;
}

.contenido {
  flex: 8;
}

.movimiento {
  position: sticky;
  top: 0;
  bottom: 0;
}

.movimiento a {
  color: #000;
  font-family: Montserrat, sans-serif;
  font-weight: bold;
  text-decoration: none;
  transition: all .2s ease-in-out;
}

.movimiento a:hover {
  color: brown;
  text-decoration: underline;
}

.movimiento h3 {
  text-align: center;
}

.pie-pagina {
  background-color: #0a141d;
  width: 100%;
}

.pie-pagina .grupo1 {
  grid-gap: 50px;
  grid-template-columns: repeat(3, 1fr);
  width: 100%;
  max-height: 1200px;
  margin: auto;
  padding: 45px 0;
  display: grid;
}

.pie-pagina .grupo1 .box figure {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

.pie-pagina .grupo1 .box img {
  width: 250px;
}

.pie-pagina .grupo1 .box h2 {
  color: #fff;
  margin-bottom: 25px;
  font-size: 20px;
}

.pie-pagina .grupo1 .box p {
  color: #efefef;
  margin-bottom: 10px;
}

.pie-pagina .grupo1 .box a {
  color: orange;
}

.pie-pagina .grupo1 .box a:hover {
  color: red;
}

.pie-pagina .grupo1 .redes a {
  color: #fff;
  text-align: center;
  background-color: #000d2033;
  width: 50px;
  height: 45px;
  margin-right: 10px;
  line-height: 45px;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.pie-pagina .grupo1 .redes a:hover {
  color: #0ff;
}

.pie-pagina .grupo2 {
  text-align: center;
  color: #fff;
  background-color: #0a1a2a;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 15px 10px;
  display: flex;
}

.pie-pagina .grupo2 small {
  font-size: 15px;
}

@media (width <= 700px) {
  header {
    flex-direction: column;
  }

  nav {
    padding: 10px 0;
  }

  .pie-pagina .grupo1 {
    grid-gap: 30px;
    grid-template-columns: repeat(1, 1fr);
    width: 90%;
    padding: 35px 0;
  }

  .menu-lateral {
    flex: 1;
  }

  .contenido {
    flex: 9;
  }

  .imagen-info img {
    width: auto\9 ;
    max-width: 100%;
    height: auto;
  }
}
